.biases {
  text-align: left;
  @media only screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

.biases-active {
  @media only screen and (min-width: 768px) {
    margin-top: 3rem;
    margin-left: 2.5rem;
  }
}

.section-info p {
  text-align: left;
  @media only screen and (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
    max-width: 20rem;
  }
  &:before {
    display: inline-block;
    width: 20px;
    height: 20px;
    color: #FCBBC0;
    content: '\21AC';
    margin-left: -26px;
    margin-right: 6px;
    font-size: 1rem;
    line-height: 1;
  }
}
