$font-sans:     'Black Han Sans', sans-serif;
$font-mono:     'Roboto Mono', monospace;

$font-size-body: 13px !default;
$font-size-small: 12px !default;

// Headings
// --------------------------------------------------

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $font-sans;
}

// Body text
// --------------------------------------------------

body {
  font-family: $font-mono;
  font-size: $font-size-body;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: #f58090;
}

p {
  line-height: 1.8;
}

li {
  line-height: 1.3;
  margin-bottom: 8px;
}

p + * {
  margin-top: 1.5rem;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

// Lists
// --------------------------------------------------

dt {
  font: 700 $font-size-small $font-sans;
  text-transform: uppercase;
  letter-spacing: 3px;
}


// Code
// --------------------------------------------------

tt,
code {
  font-family: $font-mono;
  font-size: $font-size-small;
}

pre {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $font-mono;
  font-size: $font-size-small;
}
