.nav {
  @media only screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  @media only screen and (max-width: 768px) {
    display: block;
    text-align: center;
  }
  padding-left: 0;
  padding-bottom: 1rem;
  margin: 0 auto 2rem auto;
  border-bottom: solid 1px #eee;
  max-width: 500px;
}

.nav a {
  &:not(:last-child){
    margin-right: 5px;
  }
  @media only screen and (min-width: 768px) {
    &:not(:last-child){
      margin-right: 2rem;
    }
  }
  &.active {
    color: #666;
    cursor: text;
    &:hover {
      text-decoration: none;
    }
  }
}
