.bias {
  position: relative;
  display: inline-block;
  cursor: move;
  &:before {
    display: block;
  	content: attr(data-title);
    position: absolute;
    top: 0;
    width: 100%;
    line-height: 1;
    vertical-align: middle;
    font-size: 2rem;
    font-family: $font-sans;
    letter-spacing: -2px;
    color: #fff;
    opacity: 0;
    text-shadow: 0 1px 0 rgba(0,0,0,0.1), 0 0 10px rgba(0,0,0,0.25);
    @extend .fade;
  }
}

.ults {
  display: flex;
  @media only screen and (max-width: 500px) {
    flex-wrap: wrap;
  }
}

.bias:hover:before {
  opacity: 1;
}

.bias.member {
  display: inline-block;
}

.bias.member:before {
  height: 26px;
}

.bias.idalui-sonyeo:before {
  font-size: 5rem;
  padding-top: 18%;
  @media only screen and (max-width: 500px) {
    font-size: 3rem;
  }
}

.circle {
  border-radius: 50%;
}

.bias-image {
  @extend .circle;
  border: solid 4px white;
  @media only screen and (max-width: 500px) {
    width: 100px;
  }
}

.haseul {
  top: -40px;
  left: -40px;
  z-index: 12;
  &:before {
    font-size: 2.5rem;
    padding-top: calc(60% - 26px);
  }
  @media only screen and (max-width: 500px) {
    left: -10px;
    &:before {
      font-size: 1.75rem;
    }
  }
}

.bias.member.doyoung {
  max-width: 176px;
  z-index: 11;
  top: 20px;
  left: 30px;
  &:before {
    font-size: 1.75rem;
    padding-top: calc(66% - 26px);
  }
  @media only screen and (max-width: 500px) {
    top: -70px;
    left: 70px;
  }
}

.suga {
  max-width: 210px;
  z-index: 10;
  left: 30px;
  top: -80px;
  &:before {
    padding-top: calc(60% - 26px);
  }
  @media only screen and (max-width: 500px) {
    left: 110px;
    &:before {
      font-size: 1.75rem;
    }
  }
}

.bias.member.hongjoong {
  max-width: 156px;
  top: -10px;
  left: 30px;
  &:before {
    font-size: 1.5rem;
    padding-top: 35%;
  }
  @media only screen and (max-width: 500px) {
    top: -60px;
    &:before {
      font-size: 1.75rem;
    }
  }
}

.bias.member.eunha {
  max-width: 250px;
  top: -55px;
  left: -10px;
  &:before {
    padding-top: calc(66% - 26px);
  }
  @media only screen and (max-width: 500px) {
    left: 5px;
    &:before {
      font-size: 1.75rem;
    }
  }
}
