.hero {
  display: block;
  margin: 0 auto 2rem auto;
  width: 250px;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.button {
  display: inline-block;
  padding: 10px 14px;
  border-radius: 5px;
  margin: 5px;
  background: lighten(#f58090, 5%);
  color: #fff;
  border: solid 2px lighten(#f58090, 5%);
  &:hover {
    border-color: #ddd;
    background: #fff;
    color: #000;
    text-decoration: none;
  }
}
